import React, { useEffect, useContext, useState } from "react";
import { Divider, Row, Col, Typography, List, Button, Image, Card } from "antd";
import {
  MailTwoTone,
  SmileTwoTone,
  QuestionOutlined,
  CloseOutlined,
} from "@ant-design/icons";

import axios from "axios";
import { withRouter, Link } from "react-router-dom";
import { AuthContext } from "../GlobalContext/AuthContext";
import { DataContext } from "../GlobalContext/DataContext";

import AddInstructionModal from "./AddInstructionModal";

let initialState = {};

function Dashboard({ history }) {
  let Auth = useContext(AuthContext);
  let Data = useContext(DataContext);

  const { Title } = Typography;
  const [userInfo, setUserInfo] = useState(Auth.state.userData);
  const [currentUser, setCurrentUser] = useState(null);
  const [listData, setListData] = useState([]);
  const [images, setImages] = useState(null);

  useEffect(() => {
    axios
      .post("/api/v1/users/search_current_user", {
        value: userInfo.id,
      })
      .then((res) => {
        let data = res.data;
        setCurrentUser(data);
      });
    axios
      .post("/api/v1/titles/search_titles", { value: userInfo.id })
      .then((res) => {
        // console.log(res);
        let data = res.data;
        setListData(data);
      })
      .catch((error) => console.log(error));
  }, []);

  const modalClosed = () => {
    console.log(images);
    axios
      .post("/api/v1/titles/search_titles", { value: userInfo.id })
      .then((res) => {
        // console.log(res);
        let data = res.data;
        setListData(data);
      })
      .catch((error) => console.log(error));
  };

  const viewInstruction = (instructionInfo) => {
    Data.passData(instructionInfo);
  };

  const deleteInstruction = (id) => {
    axios
      .delete("/api/v1/titles/delete_title", {
        params: {
          id: id,
        },
      })
      .then((res) => {
        axios
          .post("/api/v1/titles/search_titles", { value: userInfo.id })
          .then((res) => {
            // console.log(res);
            let data = res.data;
            setListData(data);
          });
      })
      .catch((error) => console.log(error));
  };
  return (
    <div>
      <Divider>
        <Card>
          <Title level={2}>Online Instruction Manual Saver</Title>
        </Card>
      </Divider>
      <Row>
        <Image
          height={100}
          width={120}
          src={`/api/v1/images/${
            currentUser == null ? "logo.png" : currentUser[0].profilePicture
          }`}
        />
      </Row>
      <Row>
        <Title level={4}>
          <SmileTwoTone />{" "}
          {currentUser == null
            ? ""
            : currentUser[0].firstName + " " + currentUser[0].lastName}
        </Title>
      </Row>
      <Row>
        <Title level={4}>
          <MailTwoTone /> {userInfo.email}
        </Title>
      </Row>
      <Row>
        <AddInstructionModal info={userInfo} afterClosing={modalClosed} />
      </Row>

      <Divider orientation="left"></Divider>

      <List
        size="large"
        header={<Title level={2}>Instruction List</Title>}
        footer={<div>Footer</div>}
        bordered
        dataSource={listData}
        renderItem={(item) => (
          <List.Item>
            <Col flex="auto">{item.title}</Col>

            <Col>
              <Link to={`/instructions/${item.id}`}>
                <Button type="primary" onClick={() => viewInstruction(item)}>
                  <QuestionOutlined />
                  View
                </Button>
              </Link>
              <Button type="danger" onClick={() => deleteInstruction(item.id)}>
                <CloseOutlined />
                Delete
              </Button>
            </Col>
          </List.Item>
        )}
      />

      <Divider></Divider>
    </div>
  );
}

export default withRouter(Dashboard);
